#ifndef __LERP_H__
#define __LERP_H__

#include <stdint.h>

#define min(a,b) ((a)<(b)?(a):(b))
#define max(a,b) ((a)>(b)?(a):(b))

struct LerpStage {
	int16_t slope;
	int8_t limit;
};

struct LerpProgram {
	uint8_t progressionStart;
	uint8_t loopStartAndEnd;
};

class Lerp {
	private:
		uint8_t progressionStart = 0;
		uint8_t loopStart = 0xFF;
		uint8_t loopEnd = 0xFF;
		uint8_t stageIndex = 0xFF;
		int16_t amp = 0;
		int16_t slope = 0;
		int8_t limit = -128;
	
		void loadStage() volatile;
	
	public:
		uint8_t sample() volatile {
			amp += slope;
			int8_t out = amp >> 8;

			const bool nextStage = (out < 0) ||
				(slope < 0)
					? out < limit
					: out > limit;
		
			if (nextStage) {
				out = limit;
				amp = limit << 8;
				stageIndex = stageIndex == loopEnd
					 ? loopStart
					 : stageIndex + 1;
				loadStage();
			}
		
			return out;
		}
		
		void start(uint8_t program) volatile;

		void stop() volatile {
			if (stageIndex < loopEnd) {
				stageIndex = loopEnd;
				loadStage();
			}
		}

		friend class Synth;
		
		#ifdef __EMSCRIPTEN__

		uint8_t sampleEm() { return sample(); }
		void startEm(uint8_t program) { start(program); }
		void stopEm() { stop(); }
		
		#endif // __EMSCRIPTEN__
};

#undef min
#undef max

#endif //__LERP_H__
